"use client";

import { useState } from "react";
import {
  Edit3,
  Trash2,
  Check,
  X,
  Plus,
  TrendingUp,
  TrendingDown,
  DollarSign,
} from "lucide-react";

// Peso formatter
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
  }).format(val);

export default function TransactionTable({
  transactions,
  loading,
  onAddTransaction,
  onEditTransaction,
  onDeleteTransaction,
}) {
  const [form, setForm] = useState({
    date: "",
    description: "",
    deposit: "",
    withdrawal: "",
  });

  const [editingId, setEditingId] = useState(null);
  const [rowForm, setRowForm] = useState({});
  const [showForm, setShowForm] = useState(false);

  const totalDeposits = transactions.reduce((s, t) => s + (t.deposit || 0), 0);
  const totalWithdrawals = transactions.reduce((s, t) => s + (t.withdrawal || 0), 0);
  const balance = totalDeposits - totalWithdrawals;

  const sorted = [...transactions].sort(
    (a, b) => new Date(b.date) - new Date(a.date)
  );

  const submit = (e) => {
    e.preventDefault();
    onAddTransaction({
      ...form,
      deposit: Number(form.deposit || 0),
      withdrawal: Number(form.withdrawal || 0),
      id: Date.now(),
    });
    setForm({ date: "", description: "", deposit: "", withdrawal: "" });
    setShowForm(false);
  };

  return (
    <div className="space-y-6">

      {/* SUMMARY */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <SummaryCard
          label="Total Deposits"
          value={currency(totalDeposits)}
          icon={<TrendingUp />}
          color="green"
        />
        <SummaryCard
          label="Total Withdrawals"
          value={currency(totalWithdrawals)}
          icon={<TrendingDown />}
          color="red"
        />
        <SummaryCard
          label="Current Balance"
          value={currency(balance)}
          icon={<DollarSign />}
          color="blue"
        />
      </div>

      {/* TABLE */}
      <div className="bg-white border rounded-xl shadow">
        <div className="flex justify-between items-center p-4 border-b">
          <h2 className="text-lg font-semibold">Transaction Records</h2>
          <button
            onClick={() => setShowForm(!showForm)}
            className="flex items-center gap-2 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          >
            <Plus size={16} /> Add Transaction
          </button>
        </div>

        {showForm && (
          <form
            onSubmit={submit}
            className="grid grid-cols-1 md:grid-cols-5 gap-4 p-4 bg-gray-50 border-b"
          >
            <input type="date" required className="input" value={form.date}
              onChange={(e) => setForm({ ...form, date: e.target.value })} />
            <input type="text" placeholder="Description" className="input"
              value={form.description}
              onChange={(e) => setForm({ ...form, description: e.target.value })} />
            <input type="number" placeholder="Deposit" className="input"
              value={form.deposit}
              onChange={(e) => setForm({ ...form, deposit: e.target.value })} />
            <input type="number" placeholder="Withdrawal" className="input"
              value={form.withdrawal}
              onChange={(e) => setForm({ ...form, withdrawal: e.target.value })} />
            <button className="bg-green-600 text-white rounded flex items-center justify-center gap-1">
              <Check size={16} /> Save
            </button>
          </form>
        )}

        <table className="w-full text-sm">
          <thead className="bg-gray-100">
            <tr>
              <th className="p-3 text-left">Date</th>
              <th className="p-3 text-left">Description</th>
              <th className="p-3 text-right">Deposit</th>
              <th className="p-3 text-right">Withdrawal</th>
              <th className="p-3 text-center">Actions</th>
            </tr>
          </thead>
          <tbody>
            {sorted.map((t) => (
              <tr key={t.id} className="border-t hover:bg-gray-50">
                <td className="p-3">{t.date}</td>
                <td className="p-3">{t.description || "-"}</td>
                <td className="p-3 text-right text-green-600">
                  {t.deposit ? currency(t.deposit) : "-"}
                </td>
                <td className="p-3 text-right text-red-600">
                  {t.withdrawal ? currency(t.withdrawal) : "-"}
                </td>
                <td className="p-3 flex justify-center gap-2">
                  <button
                    onClick={() => {
                      setEditingId(t.id);
                      setRowForm(t);
                    }}
                    className="icon-btn text-yellow-600"
                  >
                    <Edit3 size={16} />
                  </button>
                  <button
                    onClick={() => onDeleteTransaction(t.id)}
                    className="icon-btn text-red-600"
                  >
                    <Trash2 size={16} />
                  </button>
                </td>
              </tr>
            ))}

            {!sorted.length && (
              <tr>
                <td colSpan="5" className="p-6 text-center text-gray-500">
                  No transactions yet
                </td>
              </tr>
            )}
          </tbody>
        </table>

        {loading && (
          <div className="p-4 text-center text-gray-500">Loading...</div>
        )}
      </div>
    </div>
  );
}

/* ===== Helper Components ===== */

function SummaryCard({ label, value, icon, color }) {
  const colors = {
    green: "bg-green-50 text-green-700",
    red: "bg-red-50 text-red-700",
    blue: "bg-blue-50 text-blue-700",
  };

  return (
    <div className={`p-4 border rounded-xl flex justify-between ${colors[color]}`}>
      <div>
        <p className="text-sm">{label}</p>
        <p className="text-2xl font-bold">{value}</p>
      </div>
      {icon}
    </div>
  );
}
